<template>
    <div class="test">
        <!-- 内容 -->
        <div class="contain" style="width:1200px;margin:50px auto;display:flex">
            <div class="left" style="width:55%;border:1px solid #E4E7ED">
                <div style="height:80px;line-height:80px;text-align:center">自动检测方法
                    <el-button type="primary" style="margin-left:100px" @click="addTestshow">添加测试方法</el-button>
                </div>
                <el-table
                    :data="tableData"
                    border
                    style="width: 100%">
                    <el-table-column
                    prop="tName"
                    label="检测方法"
                    width="180">
                    </el-table-column>
                    <el-table-column
                    prop="tMethod"
                    label="方法信息"
                    width="180">
                    </el-table-column>
                    <!-- 操作 -->
                    <el-table-column
                    prop="address"
                    label="操作">
                    <template slot-scope="scope" >
                        <el-button
                        size="small"
                        @click="handleEdit(scope.$index, scope.row)">修改</el-button>

                        <el-button
                        size="small  "
                        type="danger"
                        @click="handleDelete(scope.$index, scope.row)">删除</el-button>

                        <el-button
                        size="small"
                        @click="handleRepair(scope.$index, scope.row)">修复方法</el-button>


                    </template>

                    </el-table-column>
                </el-table>

            </div>
            <div class="right" style="width:45%;border:1px solid #E4E7ED" v-show="isShow">

                <div style="height:80px;line-height:80px;text-align:center">修复方法
                    <el-button type="primary" style="margin-left:100px" @click="addRepairiShow">添加修复方法</el-button>
                    <i class="el-icon-circle-close" style="font-size:30px;float:right;cursor:pointer" @click="closeRepair"></i>
                </div>
                <el-table
                    :data="repaireList"
                    border
                    style="width: 100%">
                    <el-table-column
                    prop="rName"
                    label="修复方法"
                    width="180">
                    </el-table-column>
                    <el-table-column
                    prop="rMethod"
                    label="方法信息"
                    width="180">
                    </el-table-column>
                    <!-- 操作 -->
                    <el-table-column
                    prop="address"
                    label="操作">
                    <template slot-scope="scope" >
                        <el-button
                        size="small"
                        @click="editRepair(scope.$index, scope.row)">修改</el-button>

                        <el-button
                        size="small  "
                        type="danger"
                        @click="deleteRepair(scope.$index, scope.row)">删除</el-button>
                    </template>

                    </el-table-column>
                </el-table>

            </div>

            <!-- 修改测试方法会话框 -->
            <el-dialog title="修改信息" :visible.sync="dialogFormVisible" width="30%">
                <el-form :model="textForm">
                    <el-form ref="form" :model="repairForm" label-width="80px">
                        <el-form-item label="方法名称">
                            <el-input v-model="textForm.tName"></el-input>
                        </el-form-item>
                        <el-form-item label="方法信息">
                            <el-input v-model="textForm.tMethod"></el-input>
                        </el-form-item>

                    </el-form>

                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="updateTest">确 定</el-button>
                </div>
            </el-dialog>
            <!-- 修改修复方法会话框 -->
            <el-dialog title="修改信息" :visible.sync="repairFormVisible" width="30%">
                    <el-form ref="form" :model="repairForm" label-width="80px">
                        <el-form-item label="方法名称">
                            <el-input v-model="repairForm.rName"></el-input>
                        </el-form-item>
                        <el-form-item label="方法信息">
                            <el-input v-model="repairForm.rMethod"></el-input>
                        </el-form-item>

                    </el-form>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="repairFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="updateRepair">确 定</el-button>
                </div>
            </el-dialog>

            <!-- 增加测试方法会话框 -->
            <el-dialog
                title="增加配置信息"
                :visible.sync="addTestVisible"
                width="30%"
            >
                <el-form ref="form" :model="addTestForm" label-width="80px">
                    <el-form-item label="测试名称">
                        <el-input v-model="addTestForm.tName"></el-input>
                    </el-form-item>
                    <el-form-item label="测试方法">
                        <el-input v-model="addTestForm.tMethod"></el-input>
                    </el-form-item>

                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="addTestVisible = false">取 消</el-button>
                    <el-button type="primary" @click="addTest">确 定</el-button>
                </span>
            </el-dialog>

            <!-- 增加修复方法会话框 -->
            <el-dialog
                title="增加配置信息"
                :visible.sync="addRepairShow"
                width="30%"
            >
                <el-form ref="form" :model="addRepairForm" label-width="80px">
                    <el-form-item label="测试名称">
                        <el-input v-model="addRepairForm.rName"></el-input>
                    </el-form-item>
                    <el-form-item label="测试方法">
                        <el-input v-model="addRepairForm.rMethod"></el-input>
                    </el-form-item>

                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="addRepairShow = false">取 消</el-button>
                    <el-button type="primary" @click="addRepair">确 定</el-button>
                </span>
            </el-dialog>


        </div>
    </div>
</template>

<script>
export default {
    name:'Test',
    data(){
        return{
            tableData: [],
            //控制修复栏显示
            isShow:false,
            //控制修改测试方法会话框
            dialogFormVisible:false,
            //测试属性名
            testKeys:[],
            //修改测试方法表单
            textForm:{
                tId:'',
                tName:'',
                tMethod:''
            },
            formLabelWidth:'120px',
            //控制修改修复方法会话框
            repairFormVisible:false,
            //修改修复方法表单
            repairForm:{
                rId:0,
                rName:'',
                rMethod:''
            },
            //修复属性名
            repairKeys:[],
            //增加测试方法会话框显示
            addTestVisible:false,
            //添加测试方法表单
            addTestForm:{
                tName:'',
                tMethod:''
            },
            //添加修复方法会话框显示
            addRepairShow:false,
            //添加修复方法表单
            addRepairForm:{
                rName:'',
                rMethod:''
            },
            //当前测试信息
            textContent:{},
            //修复方法列表
            repaireList:[]
        }
    },
    methods:{
        //返回
        goIndex(){
            this.$router.push('/')
        },
        //修改测试方法会话框显示
        handleEdit(index,row){
            this.dialogFormVisible = true
            this.textContent = row
            this.testKeys = Object.keys(row)
            this.textForm = row
        },
        //提交修改测试方法信息
        updateTest(){
            // this.textContent[this.textForm.region] = this.textForm.name
            this.dialogFormVisible = false
            this.$axios.put('http://192.168.1.155:8888/TMController',this.textForm).then((msg)=>{
                if (msg.data) {
                    this.$message({
                        message: '恭喜你，修改成功',
                        type: 'success'
                    });
                    // window.location.reload()
                }else{
                    this.$message.error('修改失败');
                }
            })
        },
        //删除测试方法
        handleDelete(index,row){
            this.tableData.splice(index,1)
            this.$axios.delete('http://192.168.1.155:8888/TMController/'+row.tId).then((msg)=>{
                if (msg.data) {
                    this.$message({
                         message: '恭喜你，删除成功',
                        type: 'success'
                    });
                    // window.location.reload()
                }else{
                    this.$message.error('删除失败');
            }

            })
        },
        //添加测试方法会话框显示
        addTestshow(){
            this.addTestVisible = true
        },
        //提交测试方法
        addTest(){
            this.addTestVisible = false
            this.$axios.post('http://192.168.1.155:8888/TMController/insert',this.addTestForm).then((msg)=>{
                if (msg.data) {
                    this.$message({
                        message: '恭喜你，添加成功',
                        type: 'success'
                    });
                    this.tableData.push(this.addTestForm)
                    // window.location.reload()
                }else{
                    this.$message.error('添加失败');
                }
            })

        },
        //打开修复方法
        handleRepair(index,row){
            this.isShow = true
            this.$axios.get('http://192.168.1.155:8888/RMController').then((msg)=>{
                this.repaireList = msg.data
            })
        },
        //关闭修复方法
        closeRepair(){
            this.isShow = false
        },
        //修改修复方法
        editRepair(index,row){
            this.repairFormVisible = true
            this.repairForm = row
        },
        //提交修改修复方法表单
        updateRepair(){
            this.repairFormVisible = false
            this.$axios.put('http://192.168.1.155:8888/RMController',this.repairForm).then((msg)=>{
                if (msg.data) {
                    this.$message({
                        message: '恭喜你，修改成功',
                        type: 'success'
                    });
                }else{
                    this.$message.error('修改失败');
                }
            })
        },
        //删除修复方法
        deleteRepair(index,row){
            this.$axios.delete('http://192.168.1.155:8888/RMController/'+row.rId).then((msg)=>{
                if (msg.data) {
                    this.$message({
                        message: '恭喜你，删除成功',
                        type: 'success'
                    });
                    window.location.reload()
                }else{
                    this.$message.error('删除失败');
                }
            })
        },
        //添加修复方法会话框显示
        addRepairiShow(){
            this.addRepairShow = true
        },
        //添加修复方法表单提交
        addRepair(){
            this.addRepairShow = false
            this.$axios.post('http://192.168.1.155:8888/RMController/insert',this.addRepairForm).then((msg)=>{
                if (msg.data) {
                    this.$message({
                        message: '恭喜你，添加成功',
                        type: 'success'
                    });
                    window.location.reload()
                }else{
                    this.$message.error('添加失败');
                }
            })
        }
    },
    mounted(){
        //获取检测列表
        this.$axios.get('http://192.168.1.155:8888/TMController').then((msg)=>{
          this.tableData = msg.data
        })
    }
}
</script>

<style scoped>

</style>


